import React, { useState } from "react";

export default function BlogFilterBar() {
  const [selectedItem, setSelectedItem] = useState("Posts");

  const handleItemClick = (itemName) => {
    setSelectedItem(itemName);
  };

  return (
    <div className="container">
      <nav
        id="Navbar2"
        className="navbar navbar-expand-lg navbar-light bg-light"
      >
        <a className="navbar-brand mr-lg-4" href="#">
          <strong>{selectedItem}</strong>
        </a>
        <button
    
          className="navbar-toggler btn btn-secondary py-2"
          type="button"
          data-toggle="collapse"
          data-target="#navbarSupportedContent"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="nav2toggle">
            Filter:{" "}
            <em>
              <strong>{selectedItem}</strong>
            </em>{" "}
            <i className="bi bi-caret-down-fill"></i>
          </span>
        </button>
        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav mr-auto">
            <li
              className={`nav-item ${
                selectedItem === "All Posts" ? "active" : ""
              }`}
            >
              <a
                className={`nav-link ${selectedItem === "Posts" ? "active" : ""}`}
                href="#"
                onClick={() => handleItemClick("Posts")}
              >
                Posts <span className="sr-only">(current)</span>
              </a>
            </li>
            <li
              className={`nav-item ${
                selectedItem === "Article" ? "active" : ""
              }`}
            >
              <a
                className={`nav-link ${selectedItem === "Article" ? "active" : ""}`}
                href="#"
                onClick={() => handleItemClick("Article")}
              >
                Article
              </a>
            </li>
            <li
              className={`nav-item ${selectedItem === "Event" ? "active" : ""}`}
            >
              <a
                className={`nav-link ${selectedItem === "Event" ? "active" : ""}`}
                href="#"
                onClick={() => handleItemClick("Event")}
              >
                Event
              </a>
            </li>
            <li
              className={`nav-item ${
                selectedItem === "Education" ? "active" : ""
              }`}
            >
              <a
                className={`nav-link ${selectedItem === "Education" ? "active" : ""}`}
                href="#"
                onClick={() => handleItemClick("Education")}
              >
                Education
              </a>
            </li>
            <li
              className={`nav-item ${selectedItem === "Job" ? "active" : ""}`}
            >
              <a
                className={`nav-link ${selectedItem === "Job" ? "active" : ""}`}
                href="#"
                onClick={() => handleItemClick("Job")}
              >
                Job
              </a>
            </li>
          </ul>
          <form className="form-inline my-2">
            <button className="btn btn-secondary mr-2 " type="submit">
              Write a Post <i className="bi bi-caret-down-fill"></i>
            </button>
            <button className="btn btn-primary my-sm-0" type="submit">
            <i class="bi bi-people-fill pr-1"></i>{" "}Join Group
            </button>
          </form>
        </div>
      </nav>
    </div>
  );
}
